*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
html {
  font-size: 62.5%;
  font-family: 'Inter', sans-serif;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
#root {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
}
main {
  flex-grow: 1;
}
.todo-title__container {
  padding: 40px 0px 17px 18px;
  margin-bottom: 30px;
  box-shadow: 0px 4px 15px -7px rgba(0,0,0,0.75);
  border-radius: 0px 0px 10px 10px;
}
.todo-title {
  font-size: 1.8rem;
  margin-bottom: 6px;
}
.todo-p {
  font-size: 1.4rem;
  font-weight: 500;
}
.todo-list__container {
  display: flex;
  width: 100%;
}
.todo-ul-list__container {
  display: grid;
  justify-content: center;
  justify-items: center;
  flex-grow: 1;
  grid-template-columns: 50% 50%;
  width: 100%;
  padding: 0px 10px 0px 10px;
}
.todo-list__container li {
  position: relative;
  width: 92%;
  margin-bottom: 16px;
  padding: 8px;
  list-style: none;
  background-color: #79c3f5;
  border-radius: 10px;
  box-shadow: 0px 0px 15px -4px rgba(0,0,0,0.75);
}
.todo-list__container .todo-ul-list__container .loading {
  height: 62px;
  background-image: linear-gradient(90deg, rgb(227,237,243), rgb(195, 195, 195));
  background-size: 400% 400%;
  animation: gradientChange 2s ease-in-out infinite;
}
@keyframes gradientChange {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.todo-list__container li .card-text {
  font-size: 1.6rem;
}
.todo-list__container li .card-text--completed {
  text-decoration: line-through;
}
.todo-list__container li:hover {
  transform: scale(1.1);
  transition: 0.5s;
  cursor: pointer;
}
.todo-list__container .todo-ul-list__container li .icon-container {
  display: inline-block;
  width: 14px;
  height: 14px;
}
.todo-list__container .todo-ul-list__container li .icon-check {
  position: absolute;
  top: -7px;
  right: 32px;
  display: inline-block;
}
.todo-list__container .todo-ul-list__container li .icon-check:hover .icon-svg {
  transform: scale(1.4);
  transition: 0.5s;
  fill: green;
}  
.todo-list__container .todo-ul-list__container li .icon-close {
  position: absolute;
  top: -5.5px;
  right: 8px;
  width: 12px;
  height: 12px;
}
.todo-list__container .todo-ul-list__container li .icon-close:hover .icon-svg {
  transform: scale(1.6);
  transition: 0.5s;
  fill: red;
}
footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 60px;
  padding: 0px 20px 0px 20px;
  background-color: rgb(227,237,243,0.4);
  border-radius: 20px 20px 0px 0px;
  box-shadow: 0px -10px 31px -21px rgba(0,0,0,0.75);
}
.mobile-menu__botton {
  display: inline-block;
  width: 20px;
  height: 20px;
  background-image: url(../assets/icons/menu-icon.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  cursor: pointer;
}
.left-foot__section {
  position: relative;
}
.mobile-menu__container {
  position: absolute;
  top: -150px;
  width: 140px;
  height: 150px;
  padding: 20px;
  background-color: rgb(227,237,243);
  border-radius: 16px;
  z-index: 30;
}
.mobile-menu__container ul {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 100%;
  list-style: none;
}
.mobile-menu__container ul li {
  list-style: none;
}
.mobile-menu__container ul li a {
  font-size: 1.8rem;
  font-weight: 500;
  color: black;
  text-decoration: none;
}
.add-button {
  position: relative;
  bottom: 28px;
  display: inline-block;
  width: 50px;
  height: 50px;
  font-size: 3.8rem;
  color: #79c3f5;
  border-color: transparent;
  border-radius: 50%;
  transition: 3s;
  z-index: 1;
}
.add-button:hover {
  transform: rotate(360deg);
  cursor: pointer;
}
.righ-foot__section {
  position: relative;
}
.search-icon__button {
  display: inline-block;
  width: 20px;
  height: 20px;
  cursor: pointer;
  background-image: url(../assets/icons/search-icon.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.search-icon__button:hover {
  transform: scale(1.4);
  transition: 0.5s;
}
.search-input__modifier {
  position: absolute;
  right: -1px;
  top: -42px;
  width: 200px;
  padding: 10px;
  border-radius: 10px;  
  z-index: 30;
}
.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgb(0, 204, 255,0.5);
}
.add-todo__form {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 280px;
  height: 240px;
  background-color: white;
  border-radius: 10px;
}
.add-todo__form .text-input {
  width: 80%;
  height: 100px;
  padding: 8px;
  border-radius: 8px;
  box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
}
.add-todo__form .text-input:focus {
  outline-color: #79c3f5;
  box-shadow: 0px 0px 5px 0px #79c3f5;
}
.add-todo__form .buttons__container {
  display: flex;
  gap: 20px;
}
.add-todo__form .buttons__container .todo-form {
  width: 62px;  
  height: 40px;
  border: none;
  border-radius: 6px;
  font-size: 1.4rem;
  background-color: white;
  cursor: pointer;
}
.add-todo__form .buttons__container .todo-form--add{
  border-radius: 6px;
  font-weight: 700;
  background-color: #79c3f5;  
  box-shadow: 0px 0px 5px 0px black;
  cursor: pointer;
}
.hide {
  display: none;
}